	module.exports = {"data":{"markdownRemark":{"html":"<p>I bought a pair of cloth shoes, and we joked that it is old eight-way shoes. Nowadays, there are a lot of things on the market, everything is there, and there are all kinds of imported domestic products. This shows that the economy has developed and the market has developed. However, from a cultural point of view, it is still not high. The market is not just selling and stacking a lot of goods, but a whole set of culture. Perhaps the economic development of a society, it is not easy to make commodities rich, it is even more difficult to achieve a higher economic culture.</p>\n<p>Culture is not something that can be solved by assault. It must have history and be linked to the spiritual and cultural level of the nation. The recent cotton, cigarettes, and alcohol counterfeiting have made people feel this deeply. How to make culture grow in the growth of material goods is a major social development problem, so that society can develop in a balanced manner.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0141.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0141.md","frontmatter":{"title":"Monday, August 29"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0141.md absPath of file >>> MarkdownRemark"}}

